.application-edit {
  margin-bottom: 30px; }
  @media screen and (max-width: 47.9375em) {
    .application-edit .confirmation-pane {
      display: none; } }
  @media screen and (min-width: 48em) {
    .application-edit__pane-grey {
      padding: 40px 0;
      background: #EDF1F4; } }

.modal__instruction {
  top: 5%;
  background: white;
  width: 1040px;
  margin: auto;
  position: relative; }
  .modal__instruction h2 {
    margin: 0; }
  @media (max-width: 768px) {
    .modal__instruction {
      width: 100%; } }

.modal__instruction-content {
  position: relative;
  width: 1040px;
  overflow: auto;
  height: 85%;
  font-size: 16px; }
